
<template>
  <el-table
      :data="tableData"
      style="width: 100%"
      :default-sort = "{prop: 'date', order: 'descending'}"
  >
    <el-table-column
        prop="date"
        label="日期"
        sortable
        width="180">
    </el-table-column>
    <el-table-column
        prop="name"
        label="姓名"
        sortable
        width="180">
    </el-table-column>
    <el-table-column
        prop="address"
        label="地址"
        :formatter="formatter">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: "SortTable",
  data() {
    return {
      tableData: [{
        date: '2016-05-02',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园'
      }, {
        date: '2016-05-04',
        name: '小虎',
        address: '湖北省武汉市 九峰森林 动物园'
      }, {
        date: '2016-05-01',
        name: '小虎',
        address: '湖北省武汉市 东湖飞鸟世界 动物园'
      }, {
        date: '2016-05-03',
        name: '小虎',
        address: '湖北省武汉市 冰雪世界 动物园'
      }]
    }
  },
  methods: {
    formatter(row, column) {
      return row.address;
    }
  }
}
</script>

